.dropdown {
  z-index: 15;
  opacity: 0;
  background: linear-gradient(135deg, #0d0d0d 0%, #1a1a1a 100%);
  border: 1px solid #0f0;
  border-radius: 8px;
  flex-direction: column;
  gap: 10px;
  padding: 1em;
  transition: opacity .3s, transform .3s;
  display: none;
  position: absolute;
  top: 60px;
  right: 20px;
  transform: translateY(-10px);
  box-shadow: 0 8px 16px #0f03;
}

.dropdown.open {
  opacity: 1;
  display: flex;
  transform: translateY(0);
}

.dropdown a {
  color: #0f0;
  align-items: center;
  padding: .5em;
  font-size: 1.1em;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.dropdown a:hover {
  color: #0d0d0d;
  text-shadow: 0 0 5px #0f0;
  background-color: #0f0;
  border-radius: 4px;
}

.dropdown a:hover:before {
  transform: scale(1.5);
}
